export default `

  attribute vec3 aRandom;
  uniform float uPixelRatio;
  uniform float uTime;
  uniform float uIntensity;
  uniform float uScale;
  uniform float uSMultiplier;
  varying vec2 vUv;
  varying vec3 vPosition;


  void main() {

    vUv = uv;
    vPosition = position;

    vec3 pos = position;

    pos.x += sin(uTime * aRandom.x) * uIntensity;
    pos.y += sin(uTime * aRandom.y) * uIntensity;
    pos.z += sin(uTime * aRandom.z) * uIntensity;

    pos.x *= uScale +( 3. * sin(pos.y * 4. + uTime) * (1. - uScale));
    pos.y *= uScale +( 3. * cos(pos.z * 4. + uTime) * (1. - uScale));
    pos.z *= uScale +( 3. * sin(pos.x * 4. + uTime) * (1. - uScale));

    pos *= uScale; 

    vec4 mvPosition = modelViewMatrix * vec4( pos, 1.0 );
    gl_Position = projectionMatrix * mvPosition;
    gl_PointSize = uPixelRatio * uSMultiplier / -mvPosition.z;
  }

`;
